import UniLoadMore from '@/components/uni-load-more.vue'
export default {
	components: {
		UniLoadMore
	},
	data() {
		return {
			showPinMask: false,
			curPage: 1,
			loadingType: 'more',
			profitList: [],
			total: '',
			balance: '',
			num:'',
			radio:'',
			payPassword:'',
			
		};
	},
	onLoad() {
		//此页做下拉刷新跟上拉加载
		var _self = this
		if (!uni.getStorageSync("token")) {
			this.$base1._isLogin()
		}
		_self.getProfit()
		//获取收益提现手续费比例
		uni.request({
			url: this.baseUrl + "/withdraw-fee",
			header:{
				Authorization:uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res)
				if (this.$base1._indexOf(res.data.status)) {
					this.$base1._isLogin()
				} else if(res.data.status==1){
					this.radio = res.data.data
				}else{
					uni.showToast({
						title: res.data.message,
						icon: 'none'
					})
				}
				
			}
		})
		
		
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		// console.log(e.scrollTop)
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.curPage = 1;
		this.getProfit('refresh')

	},
	//上拉加载更多
	onReachBottom() {
		this.curPage++;
		this.getProfit('add');
	},
	methods: {
		closePinMask(){
			this.showPinMask = false
		},
		//加载奖品列表 ，带下拉刷新和上滑加载
		getProfit(type = 'add', loading) {
			//没有更多直接返回
			if (type === 'add') {
				if (this.loadingType === 'nomore') {
					return;
				}
				this.loadingType = 'loading';

			} else {
				this.loadingType = 'more'
			}
			if (type === 'refresh') {
				this.profitList = [];
			}
			//收益账户
			uni.request({
				url: this.baseUrl + "/balance-list",
				data: {
					page: this.curPage,
					count: 100000
				},
				header: {
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					 if (res.data.status == 1) {
						this.profitList = this.profitList.concat(res.data.data.List)
						this.total = res.data.data.Total
						this.balance = res.data.data.Balance
						if (this.profitList.length == 0) {
							this.loadingType = '';
						} else if (this.profitList.length >= this.total) {
							this.loadingType = 'nomore';
						} else {
							this.loadingType = 'more';
						}
						if (type === 'refresh') {
							if (loading == 1) {
								uni.hideLoading()
							} else {
								uni.stopPullDownRefresh();
							}
						}
					} else {
						uni.stopPullDownRefresh();
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
				}
			})
		},
		all(){
			this.num = this.balance
		},
		balanceWithdraw(){
			this.showPinMask = true
		},
		comfirm(){
			uni.request({
				url: this.baseUrl + "/balance-withdraw",
				data: {
					PayPassword: this.payPassword,
					Number: this.num
				},
				method:"POST",
				header:{
					Authorization:uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					 if(res.data.status==1){
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
						this.showPinMask = false
					}else{
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						})
					}
					
				}
			})
		},
		showType(type){
			if(type==1){
				return "静态奖励"
			}else if(type==2){
				return "邀请奖励"
			}else if(type==3){
				return "社区奖励"
			}else if(type==4){
				return "封号清空收益"
			}else if(type==5){
				return "平级奖励"
			}else if(type==6){
				return "全球分红"
			}else if(type==7){
				return "收益余额提现"
			}
			
		}

	}
}
